import uniNavBar from "../../../components/uni-nav-bar/uni-nav-bar.vue";

export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			swiperheight:0,
			tabIndex:0,
			tabBar:[
				{name:"退款申请",id:"going"},
				{name:"退款退货",id:"completed"}
			],
			
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res)=> {
				let height = res.windowHeight - uni.upx2px(125);
				this.swiperheight = height
			}
		});
	},
	methods: {
		back(){
			uni.navigateBack({
				delta: 1
			})
		},
		tabTabs(index){
			this.tabIndex=index;
		},
		tabChange(e){
			uni.showLoading();
			this.tabIndex=e.detail.current;
			uni.hideLoading();
		},
		tothShow(){
			uni.navigateTo({
				url: '../returnpro/returnshow',
				
			});
		},
		totkShow(){
			uni.navigateTo({
				url: '../returngoods/returngoodshow',
				
			});
		}
		
		
	}
}
